.App {
  text-align: center;
}

/* Navbar */
#navbar {
  background: black;
  color: #fff;
  overflow: auto;
}

#navbar a {
  color: #fff;
  /* font-size: 25px; */
}

#navbar h1 {
  /* color:#fff; */
  color: red;
}

#navbar h1 {
  float: left;
  padding-top: 20px;
}

#navbar ul {
  float:right;
  list-style: none;
}

#navbar ul li{
  float: left;
}

#navbar ul li a {
  display: block;
  padding: 20px;
  text-align: center;
  font-size: 20px;
}

#navbar ul li a:hover,
#navbar ul li a.current {
  color: red;
}

/* Footer Nav */
#footer-nav a {
  color: white;
  display: flex;
  justify-content: flex-start;
  font-size: 20px;
}
#footer-nav ul {
  list-style-type: none;
  margin: 20px;
  margin-left: 25px;
}

/*footer*/
#main-footer {
  text-align: center;
  background: #444;
  color: #fff;
  padding: 20px;
}
.fa {
  padding: 0px;
  font-size: 30px;
  text-align: center;
  text-decoration: none;
  margin: 5px 2px;
  border-radius: 20%;
}

.fa:hover {
    opacity: 0.7;
}
.fa-facebook {
  background: #3B5998;
  color: white;
}
.fa-twitter {
  background: #55ACEE;
  color: #333;
}
.fa-youtube {
  background: #bb0000;
  color: white;
}
.fa-instagram {
  background: #125688;
  color: white;
}

